import React, { Component } from "react";
import styled from "styled-components";
import IconDelete from "./icons/icon-delete.svg";
import ReactTable from "react-table";
import "react-table/react-table.css";
import "./table.css";

const DeleteProjectButton = styled.div`
  width: 18px;
  height: 18px;
  background: #ff62c5 url(${IconDelete}) no-repeat left 4.1px top 4.1px;
  border-radius: 50%;
  border: 0;
  cursor: pointer;
`;

class DeleteProject extends Component {
  render() {
    return <DeleteProjectButton />;
  }
}

class Table extends Component {
  render() {
    const renderDate = date => {
      return (
        date.getDate() +
        "/" +
        date.getMonth() +
        "/" +
        date.getFullYear() +
        " " +
        date.getHours() +
        ":" +
        date.getMinutes()
      );
    };
    const data = [
      {
        name: "Awesome project",
        created: new Date(2019, 6, 12, 9, 22, 20),
        id: 0
      },
      {
        name: "New project",
        created: new Date(2016, 6, 12, 12, 13, 20),
        id: 1
      },
      {
        name: "Elephant",
        created: new Date(2015, 3, 8, 9, 22, 20),
        id: 2
      },
      {
        name: "WTF project",
        created: new Date(2011, 5, 5, 8, 43, 20),
        id: 3
      }
    ];

    const columns = [
      {
        Header: <span className="label">Title</span>,
        accessor: "name"
      },
      {
        Header: <span className="label">Created</span>,
        accessor: "created",
        Cell: props => <span>{renderDate(props.value)}</span>
      },
      {
        Header: "",
        accessor: "id",
        width: 54,
        style: {
          padding: "12px 36px 12px 0"
        },
        headerStyle: {
          padding: "12px 36px 12px 0"
        },
        Cell: props => <DeleteProject projectId={props.id} />
      }
    ];

    return (
      <div>
        <ReactTable
          data={data}
          columns={columns}
          showPagination={false}
          defaultPageSize={data.length}
        />
      </div>
    );
  }
}

export default Table;
